
#app{
  margin: 0px;
  padding: 0px;
}

.information{
    display: block;
    float: left;
    width: 25%;
    background-color: gray;
}
.headimg{
    width: 100%;
    height: 300px;
    background-color: black;
}
.intro{
    text-align: center;
}
.intro h1{
    margin-top: 3rem;
    text-align: center;
}
.intro p{
    text-align: center;
    font-size: 1.2rem;
}
.list-bar{
    width: 100%;
}
.list-bar a{
    text-decoration: none;
}
.list-select :hover{
    color: brown;
}
.list-select{
    color: brown;
}
.list-bar ul{
    width: 100%;
    padding: 0px;
}
.list-bar li{
    list-style: none;
    text-align: center;
    margin-left: 10%;
    margin-top: 1.7rem;
    border-bottom: 1px solid black;
    padding-bottom: 0.4rem;
    width: 80%;
    font-size: 1.3rem;
}

.blog-content{
    display: block;
    float: left;
    width: 75%;
}














































